:root {
  --font-unica: "neue-haas-unica", sans-serif;
  --font-source: "source-code-pro", monospace;
  --font-diwany: "diwany";
}

@font-face {
  font-family: "diwany";
  src: url("/fonts/diwany.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}
:root {
  --green: rgb(59, 65, 38);
  --lightgreen: rgba(59, 65, 38, 0.5);
  --tan: rgb(255, 254, 244);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 0.75rem;
  text-transform: uppercase;
  line-height: 1.2;
  color: var(--green);
  background: var(--tan);
}

button {
  display: inline-block;
  border: solid 1px var(--lightgreen);
  border-radius: 10px;
  padding: 2px 7px;
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  text-align: center;
  text-transform: uppercase;
  background: var(--tan);
  color: var(--green);
  cursor: pointer;
  transition: color 300ms ease;
}

button:hover {
  color: var(--tan);
  background: var(--green);
}

button.active {
  color: var(--tan);
  background-color: var(--green);
}

.containers {
  padding: 0.5rem;
}
.containers__pnb {
  padding: 0.5rem 0.5rem 0 0.5rem;
}
.containers__pnt {
  padding: 0 0.5rem 0.5rem 0.5rem;
}
.containers__plr {
  padding: 0 0.5rem;
}

h1,
h2,
h3 {
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
  transition: opacity 150ms ease-in-out;
}

a:hover {
  opacity: 0.8;
  text-decoration: none;
  color: inherit;
}

em {
  font-family: inherit;
  font-weight: 200;
  font-style: italic;
}

.arabic {
  font-style: normal;
  font-weight: 400;
  font-size: 1.125rem;
  text-align: right;
}

header {
  font-family: var(--font-unica);
  font-style: normal;
  font-weight: 600;
  height: 10vh;
}
header .branding {
  position: absolute;
  z-index: 99;
}

.desktop-nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 0.5rem;
}
@media (max-width: 39.9375em) {
  .desktop-nav {
    display: none;
  }
}
.desktop-nav__column1 {
  grid-column-start: 2;
}

.mobile-nav__toggle {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  z-index: 99;
  cursor: pointer;
}
.mobile-nav__menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 80px 8px 8px 8px;
  background-color: var(--tan);
  display: none;
  z-index: 9;
}
.mobile-nav__list {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.mobile-nav__list a {
  font-size: 1rem;
}
.mobile-nav__logo {
  width: 100%;
  text-align: right;
}
.mobile-nav__logo img {
  width: 50%;
}
@media (min-width: 40em) {
  .mobile-nav {
    display: none;
  }
}

.hero {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: flex-end;
  gap: 0.5rem;
  height: 90vh;
  font-family: var(--font-unica);
  font-style: normal;
  font-weight: 600;
  padding-bottom: 0.5rem;
}
.hero__info {
  font-size: 1.5rem;
  text-transform: none;
  line-height: 1;
}
.hero__info img {
  width: 2rem;
}
.hero__image {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.hero__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
@media (max-width: 39.9375em) {
  .hero {
    display: flex;
    flex-direction: column-reverse;
  }
}

.gallery {
  font-family: var(--font-source);
  font-weight: 200;
}
.gallery__tool {
  position: sticky;
  top: 0;
  background: var(--tan);
  padding: 0.5rem 0;
  margin-bottom: 1rem;
  border-bottom: solid 1px var(--lightgreen);
}
.gallery__tool button {
  text-transform: none;
}
@media (min-width: 40em) {
  .gallery__tool button:not(:last-child) {
    margin-right: 0.375rem;
  }
}
.gallery__grid {
  column-count: 4;
  gap: 0.5rem;
}
@media (max-width: 63.9375em) {
  .gallery__grid {
    column-count: 3;
  }
}
@media (max-width: 39.9375em) {
  .gallery__grid {
    column-count: 2;
  }
}
.gallery__entry {
  padding-bottom: 1rem;
  break-inside: avoid;
}
.gallery__entry a:hover {
  opacity: 1;
}
@media (max-width: 39.9375em) {
  .gallery__entry {
    padding-bottom: 1rem;
  }
}
.gallery__info {
  display: grid;
  grid-template-columns: 75% 25%;
}
@media (max-width: 39.9375em) {
  .gallery__info {
    grid-template-columns: none;
    grid-template-rows: auto;
    gap: 1rem;
  }
}
.gallery__title {
  font-family: var(--font-unica);
  font-style: normal;
  font-weight: 600;
}
.gallery__button {
  justify-self: flex-end;
}
@media (max-width: 39.9375em) {
  .gallery__button {
    justify-self: flex-start;
  }
}
.gallery__image {
  padding-bottom: 0.5rem;
}
.gallery__image img {
  max-width: 100%;
  border-radius: 5px;
}

.featherlight .featherlight-inner {
  height: 90vh;
}
@media (max-width: 39.9375em) {
  .featherlight .featherlight-inner {
    width: 100%;
    height: auto;
    padding: 0.5rem;
  }
}

.featherlight .featherlight-content {
  padding: 0;
  border-bottom: none;
  background: none;
}

.featherlight .featherlight-close-icon {
  background: var(--green);
  color: var(--tan);
  top: 0.5rem;
  right: 0.5rem;
  border-radius: 30px;
}